import { } from "react-dom"
import { Route, Switch } from 'react-router-dom'

import './App.css'

import Home from "./pages/Home"
import Rooms from "./pages/Rooms"
import SingleRoom from "./pages/SingleRoom"
import Error from "./pages/Error"

import Navbar from "./components/Navbar"


function App() {
    const ghPagesUrl = "https://antonvasilenko00.github.io/react-beach-resort"
    const homePageUrl = ghPagesUrl + '/'
    const roomsPageUrl = ghPagesUrl + '/rooms'
    const singleRoomPageUrl = ghPagesUrl + '/rooms/:slug'
  return (
    <>
      <Navbar />
      <Switch>
        <Route exact path={homePageUrl} component={Home} />
        <Route exact path={roomsPageUrl} component={Rooms} />
        <Route exact path={singleRoomPageUrl} component={SingleRoom} />
        <Route component={Error} />
      </Switch>
    </>
  );
}

export default App;
